// frontend/src/components/TransferRequestForm.tsx

import React, { useState } from 'react';
import axios from 'axios';
import { TransferRequest, PriorityLevel } from '../types/transfer';

const API_BASE_URL = 'http://localhost:3000/api/v1';

const SPECIALTIES = ['General', 'ICU', 'Pediatric', 'HDU', 'CCU', 'NICU', 'Emergency beds', 'PACU', 'PICU'];
const PRIORITIES: PriorityLevel[] = ['Low', 'Medium', 'High', 'Emergency'];

const TransferRequestForm: React.FC = () => {
    const [formData, setFormData] = useState<TransferRequest>({
        patient_name: '',
        from_ward: '',
        required_specialty: 'General',
        priority: 'Medium',
        clinical_notes: ''
    });

    const [isSubmitting, setIsSubmitting] = useState(false);
    const [message, setMessage] = useState<{ type: 'success' | 'error', text: string } | null>(null);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setIsSubmitting(true);
        setMessage(null);

        try {
            const token = localStorage.getItem('authToken');
            await axios.post(`${API_BASE_URL}/transfers`, formData, {
                headers: { Authorization: `Bearer ${token}` }
            });

            setMessage({ type: 'success', text: 'Transfer request submitted successfully!' });
            // Reset form
            setFormData({ patient_name: '', from_ward: '', required_specialty: 'General', priority: 'Medium', clinical_notes: '' });
        } catch (err) {
            setMessage({ type: 'error', text: 'Failed to submit request. Please try again.' });
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <div className="bg-white p-6 rounded-lg shadow-md border border-gray-200">
            <h2 className="text-xl font-bold mb-4 text-gray-800">New Transfer Request</h2>
            
            <form onSubmit={handleSubmit} className="space-y-4">
                {/* Patient Name */}
                <div>
                    <label className="block text-sm font-medium text-gray-700">Patient Name / ID</label>
                    <input
                        type="text"
                        required
                        className="mt-1 block w-full border rounded-md p-2"
                        value={formData.patient_name}
                        onChange={(e) => setFormData({...formData, patient_name: e.target.value})}
                    />
                </div>

                <div className="grid grid-cols-2 gap-4">
                    {/* From Ward */}
                    <div>
                        <label className="block text-sm font-medium text-gray-700">Current Ward</label>
                        <input
                            type="text"
                            required
                            className="mt-1 block w-full border rounded-md p-2"
                            value={formData.from_ward}
                            onChange={(e) => setFormData({...formData, from_ward: e.target.value})}
                        />
                    </div>

                    {/* Required Specialty */}
                    <div>
                        <label className="block text-sm font-medium text-gray-700">Needed Specialty</label>
                        <select 
                            className="mt-1 block w-full border rounded-md p-2"
                            value={formData.required_specialty}
                            onChange={(e) => setFormData({...formData, required_specialty: e.target.value})}
                        >
                            {SPECIALTIES.map(s => <option key={s} value={s}>{s}</option>)}
                        </select>
                    </div>
                </div>

                {/* Priority */}
                <div>
                    <label className="block text-sm font-medium text-gray-700">Priority Level</label>
                    <div className="flex space-x-4 mt-2">
                        {PRIORITIES.map(p => (
                            <label key={p} className="flex items-center">
                                <input
                                    type="radio"
                                    name="priority"
                                    value={p}
                                    checked={formData.priority === p}
                                    onChange={() => setFormData({...formData, priority: p})}
                                    className="mr-2"
                                />
                                <span className={p === 'Emergency' ? 'text-red-600 font-bold' : ''}>{p}</span>
                            </label>
                        ))}
                    </div>
                </div>

                {/* Notes */}
                <div>
                    <label className="block text-sm font-medium text-gray-700">Clinical Notes</label>
                    <textarea
                        className="mt-1 block w-full border rounded-md p-2"
                        rows={3}
                        value={formData.clinical_notes}
                        onChange={(e) => setFormData({...formData, clinical_notes: e.target.value})}
                    />
                </div>

                {message && (
                    <div className={`p-3 rounded ${message.type === 'success' ? 'bg-green-100 text-green-700' : 'bg-red-100 text-red-700'}`}>
                        {message.text}
                    </div>
                )}

                <button
                    type="submit"
                    disabled={isSubmitting}
                    className="w-full bg-indigo-600 text-white py-2 rounded-md hover:bg-indigo-700 disabled:bg-gray-400"
                >
                    {isSubmitting ? 'Submitting...' : 'Submit Transfer Request'}
                </button>
            </form>
        </div>
    );
};

export default TransferRequestForm;