import React, { useState, useEffect, useCallback } from 'react';
import axios from 'axios';
import BedCard from '../components/BedCard';
import TransferRequestForm from '../components/TransferRequestForm';
import TransferRequestList from '../components/TransferRequestList';
import HospitalCensus from '../components/HospitalCensus';
import AddBedForm from '../components/AddBedForm';

const API_BASE_URL = 'http://localhost:3000/api/v1';

const SPECIALTY_OPTIONS = [
    'All', 'General', 'ICU', 'Pediatric', 'HDU', 
    'CCU', 'NICU', 'Emergency beds', 'PACU', 'PICU',
    'Maternity'
];

const AdminTools = ({ token }) => {
    const [targetUser, setTargetUser] = useState('');
    const [newPass, setNewPass] = useState('');
    const [status, setStatus] = useState({ type: '', msg: '' });

    const handleAdminReset = async (e) => {
        e.preventDefault();
        try {
            await axios.post(`${API_BASE_URL}/auth/admin-reset-password`, 
                { targetUsername: targetUser, newPassword: newPass },
                { headers: { Authorization: `Bearer ${token}` } }
            );
            setStatus({ type: 'success', msg: `Password for ${targetUser} updated successfully!` });
            setTargetUser(''); setNewPass('');
        } catch (err) {
            setStatus({ type: 'error', msg: err.response?.data?.error || "Reset failed" });
        }
    };

    return (
        <section className="bg-white p-6 rounded-xl shadow-sm border-2 border-red-50 mt-10">
            <h2 className="text-xl font-bold text-red-700 mb-2 flex items-center">
                <span className="mr-2">üîê</span> Admin: Staff Password Override
            </h2>
            <p className="text-sm text-gray-500 mb-4">Manual override for staff experiencing 401 Authentication errors.</p>
            {status.msg && (
                <div className={`mb-4 p-3 rounded-lg text-sm font-bold ${status.type === 'success' ? 'bg-green-100 text-green-700' : 'bg-red-100 text-red-700'}`}>
                    {status.msg}
                </div>
            )}
            <form onSubmit={handleAdminReset} className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <input 
                    placeholder="Exact Username" 
                    className="border p-2 rounded-lg outline-none focus:ring-2 focus:ring-red-400"
                    value={targetUser} required
                    onChange={(e) => setTargetUser(e.target.value)}
                />
                <input 
                    type="password"
                    placeholder="New Secure Password" 
                    className="border p-2 rounded-lg outline-none focus:ring-2 focus:ring-red-400"
                    value={newPass} required
                    onChange={(e) => setNewPass(e.target.value)}
                />
                <button className="bg-red-600 text-white px-4 py-2 rounded-lg font-bold hover:bg-red-700 transition-all shadow-md">
                    Update Credentials
                </button>
            </form>
        </section>
    );
};

const DashboardPage = () => {
    const [beds, setBeds] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    const [error, setError] = useState(null);
    const [selectedSpecialty, setSelectedSpecialty] = useState('All');
    
    // --- NEW STATES FOR SEARCH & VIEW LIMIT ---
    const [searchQuery, setSearchQuery] = useState('');
    const [showAll, setShowAll] = useState(false);
    const BED_LIMIT = 6; 

    const user = JSON.parse(localStorage.getItem('user') || '{}');
    const token = localStorage.getItem('authToken');

    const fetchBedData = useCallback(async (specialty) => {
        setIsLoading(true);
        setError(null);
        try {
            let url = `${API_BASE_URL}/beds`;
            if (specialty && specialty !== 'All') url += `?specialty_type=${specialty}`;
            
            const response = await axios.get(url, {
                headers: { 'Authorization': `Bearer ${token}` }
            });
            setBeds(response.data);
        } catch (err) {
            if (err.response?.status === 401) setError("Session expired. Please log in again.");
            else setError('Could not load bed data.');
        } finally {
            setIsLoading(false);
        }
    }, [token]);

    useEffect(() => {
        if (token) fetchBedData(selectedSpecialty);
        else window.location.href = '/';
    }, [selectedSpecialty, fetchBedData, token]);

    // --- SEARCH LOGIC ---
    const filteredBeds = beds.filter(bed => 
        bed.bed_number?.toLowerCase().includes(searchQuery.toLowerCase()) ||
        bed.patient_name?.toLowerCase().includes(searchQuery.toLowerCase()) ||
        bed.ward_name?.toLowerCase().includes(searchQuery.toLowerCase())
    );

    // --- PAGINATION LOGIC ---
    const visibleBeds = showAll ? filteredBeds : filteredBeds.slice(0, BED_LIMIT);

    const handleLogout = () => {
        localStorage.clear();
        window.location.href = '/';
    };

    if (error) return (
        <div className="flex items-center justify-center h-screen bg-gray-100">
            <div className="bg-white p-8 rounded-xl shadow-xl border border-red-200 text-center">
                <p className="text-red-600 font-bold text-lg mb-4">‚ö†Ô∏è {error}</p>
                <button onClick={handleLogout} className="bg-indigo-600 text-white px-6 py-2 rounded-lg font-bold">Return to Login</button>
            </div>
        </div>
    );

    return (
        <div className="min-h-screen bg-gray-50">
            <header className="bg-indigo-900 text-white p-4 shadow-lg flex justify-between items-center sticky top-0 z-50">
                <h1 className="text-2xl font-bold flex items-center gap-2">üè• No Bed Syndrome</h1>
                <div className="flex items-center space-x-6">
                    <div className="text-right">
                        <p className="text-sm font-medium">{user?.username}</p>
                        <span className={`text-[10px] px-2 py-0.5 rounded-full font-bold uppercase ${user?.role === 'ADMIN' ? 'bg-red-500 text-white' : 'bg-green-500 text-white'}`}>
                            {user?.role || 'Staff'}
                        </span>
                    </div>
                    <button onClick={handleLogout} className="bg-red-500 hover:bg-red-600 px-4 py-2 rounded text-sm font-bold transition-all shadow-md">Logout</button>
                </div>
            </header>

            <main className="p-6 max-w-[1600px] mx-auto">
                <HospitalCensus beds={beds} />

                {/* --- SEARCH BAR SECTION --- */}
                <div className="bg-white p-4 rounded-xl shadow-sm border border-gray-100 mb-6 flex flex-col md:flex-row gap-4">
                    <div className="relative flex-1">
                        <span className="absolute left-3 top-2.5">üîç</span>
                        <input 
                            type="text"
                            placeholder="Search by Bed #, Patient, or Ward..."
                            className="w-full pl-10 pr-4 py-2 border rounded-lg focus:ring-2 focus:ring-indigo-500 outline-none"
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                        />
                    </div>
                    <div className="flex items-center gap-2 bg-gray-50 px-3 rounded-lg border">
                        <label className="text-xs font-bold text-gray-500 uppercase">Specialty:</label>
                        <select 
                            value={selectedSpecialty}
                            onChange={(e) => setSelectedSpecialty(e.target.value)}
                            className="bg-transparent py-2 outline-none text-sm font-semibold cursor-pointer"
                        >
                            {SPECIALTY_OPTIONS.map(opt => <option key={opt} value={opt}>{opt}</option>)}
                        </select>
                    </div>
                </div>

                <div className="grid grid-cols-1 xl:grid-cols-12 gap-6">
                    <div className="xl:col-span-8 space-y-6">
                        <section className="bg-white p-6 rounded-xl shadow-sm border border-gray-100">
                            <h2 className="text-xl font-bold text-gray-800 mb-4 flex justify-between">
                                Bed Inventory 
                                <span className="text-sm font-normal text-gray-400">Showing {visibleBeds.length} of {filteredBeds.length}</span>
                            </h2>

                            {isLoading ? (
                                <div className="text-center py-20 animate-pulse text-indigo-700 italic">Synchronizing...</div>
                            ) : (
                                <>
                                    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                                        {visibleBeds.map(bed => (
                                            <BedCard 
                                                key={bed.bed_id} 
                                                bedData={bed} 
                                                onStatusChange={() => fetchBedData(selectedSpecialty)}
                                                token={token}
                                                userRole={user?.role}
                                            />
                                        ))}
                                    </div>

                                    {/* --- SHOW MORE / LESS BUTTONS --- */}
                                    <div className="mt-8 flex justify-center border-t pt-6">
                                        {!showAll && filteredBeds.length > BED_LIMIT ? (
                                            <button 
                                                onClick={() => setShowAll(true)}
                                                className="bg-indigo-600 text-white px-10 py-2 rounded-full font-bold hover:bg-indigo-700 transition-all shadow-lg"
                                            >
                                                Show All Beds ({filteredBeds.length})
                                            </button>
                                        ) : filteredBeds.length > BED_LIMIT && (
                                            <button 
                                                onClick={() => setShowAll(false)}
                                                className="text-indigo-600 font-bold hover:underline"
                                            >
                                                Show Less
                                            </button>
                                        )}
                                    </div>
                                </>
                            )}
                        </section>

                        {user?.role === 'ADMIN' && <AdminTools token={token} />}
                    </div>

                    <div className="xl:col-span-4 space-y-6">
                        <section className="sticky top-24 space-y-6">
                            <AddBedForm 
                                onBedAdded={() => fetchBedData(selectedSpecialty)} 
                                specialties={SPECIALTY_OPTIONS} 
                            />
                            <TransferRequestForm />
                            <TransferRequestList />
                        </section>
                    </div>
                </div>
            </main>
        </div>
    );
};

export default DashboardPage;